
































































































































.nav-bar {
  border-radius: 0 0 30px 30px;
  position: fixed;
  width: 100%;
  .van-nav-bar__left:after,
  .van-nav-bar__right::before {
    content: '';
    display: inline-flex;
    vertical-align: middle;
    height: 40px;
  }
  .van-nav-bar__left:after {
    border-right: 1px solid rgba(255, 255, 255, 0.1);
    padding-right: 16px;
  }
  .van-nav-bar__right::before {
    border-left: 1px solid rgba(255, 255, 255, 0.1);
    padding-left: 16px;
  }
}
.page {
  padding: 64px 25px;

  &-content {
    will-change: scroll-position;
    overflow: auto;
    height: 100%;
    position: relative;
    &:not(&--no-margin) {
      margin: 30px 0;
    }
  }
}

.toolbar {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #3c1b49;
  padding: 10px 25px;
  box-sizing: border-box;
}
.left-panel {
  height: 100%;
  padding: 25px;
  box-sizing: border-box;
  color: rgba(white, 0.8);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.panel-footer {
  color: rgba(white, 0.5);
  font-size: 0.85rem;
  line-height: 1.5;
  a {
    color: #bf9ef6;
  }
}
.nav {
  &-item {
    padding: 20px 0;
  }
  &-link {
    color: inherit;
  }
}
